import React from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";

const Login = () => {
  const dispatch = useDispatch();
  return (
    <div>
     <Link to='/'>
          <button onClick={() => dispatch({ type: "login" })}>Login</button>
     </Link>
    </div>
  );
};

export default Login;